                                </a>
                            </div>
                        </div>
                    
                    
                    <!-- <ol class="breadcrumb m-0">
                        <li style="cursor:pointer" class="breadcrumb-item" @click="imprimir_listagem()">Imprimir</li>
                    </ol>                     -->
                </div>
            </div>
        </div>
    </div>
    <!-- end page title -->
    <div class="row" style="padding:10px">
        <div class="col-sm-12 col-md-1">                  
            <label style="width:100%" >Mostrar  
            <select class="form-control" v-model="filtro.per_page" >
                <option :value="10">10</option>
                <option :value="25">25</option>
                <option :value="50">50</option>
                <option :value="100">100</option>
            </select>
            </label>                  
        </div>        
        <div class="col-sm-12 col-md-2">
            <label style="width:100%">Entrega De:
                <date-picker  v-model="filtro.devolucao_data_inicial" :format="'DD/MM/YYYY'" :disabledDate="desabilitaDataFuturaVencimento"></date-picker>
            </label>

        </div>
        <div class="col-sm-12 col-md-2">
            <label style="width:100%">Entrega Até:
                <date-picker  v-model="filtro.devolucao_data_final" :format="'DD/MM/YYYY'" :disabledDate="desabilitaDataPassadaVencimento"></date-picker>
            </label>
        </div>

        <div class="col-sm-12 col-md-4">
            <label style="width:100%" >Cliente:
                <vue-select :options="parceiros" :reduce="c => c.id" @input="navegar(1)" placeholder="Todos"
                        @search="buscarParceiroDebounce" v-model="filtro.parceiro_id" label="apelido" >
                    <div slot="no-options">Nenhum item para mostrar! Começe digitar para pesquisar</div>
                </vue-select>
            </label>                  
        </div>
        
        <div class="col-sm-12 col-md-3">
            <label style="width:100%">Buscar por:
                <input autofocus type="text" class="form-control" placeholder="Codigo / Descrição" v-model="filtro.texto"  >
            </label>                  
        </div>

        
    </div>
    <div class="row">
        <div class="col-12 mb-10" v-if="clientes_diferentes">
            <mensagem-componente titulo="Fatura não pode ser gerada!" erros="Para gerar fatura, todos os serviços selecionados devem ser do mesmo cliente" :fechar="false"></mensagem-componente>
        </div>
        <div class="col-9 mb-10">
            <strong>{{faturaModal.ordens_servico.length}} Itens Selecionados: </strong> {{$store.state.moeda.sigla}} {{faturaModal.valor_total | currency($store.state.moeda)}}<br>            
        </div>
        <div class="col-3" v-show="faturaModal.ordens_servico.length > 0 && !clientes_diferentes">
            <button type="button" class="btn btn-primary waves-effect waves-light w-sm" title="Gerar Fatura" data-toggle="modal" data-target="#modal-gerar-fatura"
                @click="abrirModalFatura()">
                    <i class="bx bx-receipt d-block font-size-16"></i> Gerar Fatura
                </button>
        </div>
        <div class="col-12">
            <div class="card">
                <div class="card-body">
                    
                    <div class="table-responsive">
                        <table class="table mb-0" >
                            <thead>
                            <tr>
                                <th>
                                    <input type="checkbox" v-model="baixar_todos" @change="selecionarTodos()">
                                </th>
                                <th>#</th>
                                <th>Cliente</th>
                                <th>Descrição</th>
