                                <th>Entrega</th>
                                <th>Valor</th>
                                <th>Ações</th>
                            </tr>
                            </thead>
                            <tbody v-for="(dado, index) in dados" :key="index">
                                <tr >
                                    <td>
                                        <input type="checkbox" v-model="dado.baixar" @change="selecionouItem()">
                                    </td>                                                
                                    <td>{{dado.id}}</td>
                                    <td>
                                        <span @click="setClienteFiltro(dado.parceiro)" style="cursor:pointer">
                                            {{dado.parceiro ? dado.parceiro.apelido : ' - '}} 
                                        </span>
                                    </td>
                                    <td>{{dado.descricao}} </td>
                                    <td>{{dado.tipo}} </td>
                                    <td>{{dado.devolucao_data | timeformat}} </td>
                                    <td style="white-space: nowrap">{{dado.valor_total | currency($store.state.moeda)}} </td>
                                    <td> 
                                        <button type="button" class="btn btn-outline-primary waves-effect waves-light" style="padding: 3px 12px 3px 12px;" title="Ver Detalhes"
                                            data-toggle="collapse" :href="'#collapse-servicos'+dado.id">
                                            <i class="mdi mdi-eye-outline  font-size-12"></i>
                                        </button> 
                                    </td>
                                </tr>
                                <tr class="collapse" :id="'collapse-servicos'+dado.id" >
                                    <td></td>
                                    <td colspan="7">
                                        <div class="table-responsive">
                                                <table class="table table-nowrap table-centered mb-0">
                                                    <thead>
                                                        <tr>
                                                            <th scope="col">Id</th>
                                                            <th scope="col">Serviço</th>
                                                            <th scope="col">Descrição</th>
                                                            <th scope="col">Valor Unit</th>
                                                            <th scope="col">Qtde</th>
                                                            <th scope="col">Valor</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        <tr v-for="s in dado.servicos" :key="s.id">
                                                            <td>
                                                                <div class="text-muted">{{s.id}}</div>
                                                            </td>
                                                            <td>
                                                                <div class="text-muted ellipsis">{{s.servico_item.nome}}</div>
                                                            </td>
                                                            <td>
                                                                <div class="text-muted ellipsis">{{s.descricao}}</div>
                                                            </td>
                                                            <td>
                                                                <div class="text-muted">{{s.valor_unitario | currency($store.state.moeda)}}</div>
                                                            </td>
                                                            <td>
                                                                <div class="text-muted">{{s.qtde | qtde(s.servico_item.medida.decimais)}} {{s.servico_item.medida.sigla}}</div>
                                                            </td>
                                                            <td>
                                                                <div class="text-muted">{{s.valor | currency($store.state.moeda)}}</div>
                                                            </td>
                                                        </tr>
                                                        
                                                    </tbody>
                                                </table>
                                            </div>
                                    </td>
                                </tr>
                            </tbody>           
                            <tfoot>
                                <tr>
                                    <td colspan="8"> 
                                        <paginacao-componente :source="paginacao" @navigate="navegar($event)"></paginacao-componente>
                                    </td>
                                </tr>
                            </tfoot>
                        </table>
                    </div>
                </div>
            </div>
        </div> 
    </div> 

    <div class="modal fade" id="modal-gerar-fatura" tabindex="-1" aria-labelledby="modal-gerar-fatura-title" aria-hidden="true" style="display: none;">
        <div class="modal-dialog modal-lg modal-dialog-scrollable" style="min-height:450px">
            <div class="modal-content">
                <div class="modal-header">
                    <h4 class="modal-title mt-0" id="modal-gerar-faturaTitle"> Gerar Fatura de Cobrança </h4>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">×</span>
                    </button>
                </div>
                <form class="modal-body" @submit.prevent="salvarFatura()" v-if="faturaModal.parceiro">
                    <div class="row">

                        <label class="col-sm-3 col-form-label" >Cliente</label>
                        <div class="col-sm-9">
                            {{faturaModal.parceiro.id}}: {{faturaModal.parceiro.razao_social}}
                        </div>

                        <label class="col-sm-3 col-form-label">Valor Total</label>
                        <div class="col-sm-9">
                            {{$store.state.moeda.simbolo}} {{faturaModal.valor_total | currency($store.state.moeda)}}
                        </div>

                        <label class="col-sm-3 col-form-label">Vencimento</label>
                        <div class="col-sm-9">
                            <container-componente :validator="$v.faturaModal.parcelas.$each[0].data_vencimento">
                                <date-picker  v-model="faturaModal.parcelas[0].data_vencimento" :format="'DD/MM/YYYY'" :disabledDate="desabilitaDataPassada"></date-picker>
                            </container-componente>
                        </div>

                        <label class="col-sm-3 col-form-label">Observações</label>
                        <div class="col-sm-9">
                            <container-componente :validator="$v.faturaModal.historico">
                                <textarea class="form-control" rows="4" placeholder="Observações adicionais" 
                                    v-model="faturaModal.historico" @blur="$v.faturaModal.historico.$touch()"
                                ></textarea>
                            </container-componente>
                        </div>

                        <label class="col-sm-3 col-form-label">Itens</label>
                        <div class="col-sm-12">
                            <div class="table-responsive">
                                <table class="table mb-0" >
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Descrição</th>
                                        <th>Tipo</th>
                                        <th>Entrega</th>
                                        <th>Valor</th>
                                        <th>Detalhes</th>
                                    </tr>
                                    </thead>
                                    <tbody v-for="(dado, index) in faturaModal.ordens_servico" :key="index">
                                        <tr >
                                            <td>{{dado.id}}</td>
                                            <td>{{dado.descricao}} </td>
                                            <td>{{dado.tipo}} </td>
                                            <td>{{dado.devolucao_data | timeformat}} </td>
                                            <td style="white-space: nowrap">{{dado.valor_total | currency($store.state.moeda)}} </td>
                                            <td> 
                                                <button type="button" class="btn btn-outline-primary waves-effect waves-light" style="padding: 3px 12px 3px 12px;" title="Ver Detalhes"
                                                    data-toggle="collapse" :href="'#collapse-servicos-modal'+dado.id">
                                                    <i class="mdi mdi-eye-outline  font-size-12"></i>
                                                </button> 
                                            </td>
                                        </tr>
                                        <tr class="collapse" :id="'collapse-servicos-modal'+dado.id" >
                                            <td></td>
                                            <td colspan="4">
                                                <table class="table table-nowrap table-centered mb-0">
                                                    <thead>
                                                        <tr>
                                                            <th scope="col">Id</th>
                                                            <th scope="col">Serviço</th>
                                                            <th scope="col">Descrição</th>
                                                            <th scope="col">Valor Unit</th>
                                                            <th scope="col">Qtde</th>
                                                            <th scope="col">Valor</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>                                                        
                                                        <tr v-for="s in dado.servicos" :key="s.id">
                                                            <td scope="col">{{s.id}}</td>
                                                            <td scope="col">{{s.servico_item ? s.servico_item.nome : 'Serviço'}}</td>
                                                            <td scope="col">{{s.descricao}}</td>
                                                            <td scope="col">{{s.valor_unitario | currency($store.state.moeda)}}</td>
                                                            <td scope="col">{{s.qtde | qtde(s.servico_item ? s.servico_item.medida.decimais : 1 )}} {{s.servico_item ? s.servico_item.medida.sigla : 'Un'}}</td>
                                                            <td scope="col">{{s.valor | currency($store.state.moeda)}}</td>
                                                        </tr>
                                                    </tbody>
                                                </table>
                                                
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>


                    </div>

                    
                </form>
                <div class="modal-footer row">
                    <div class="col-md-12" v-if="erroModal || sucessoModal">
                        <mensagem-componente titulo="Erro!"  :erros="erroModal" @fechar="erroModal = null"></mensagem-componente>
                        <mensagem-componente  :sucesso="sucessoModal" @fechar="sucessoModal = null"></mensagem-componente>
                    </div>
                
                
                    <div class="col-md-12 text-right">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">  <i class="mdi mdi-close-circle font-size-18"></i> Fechar</button>                        
                        <button type="button" class="btn btn-primary" v-if="!faturaModal.id" @click.prevent="salvarFatura()"><i class="mdi mdi-content-save font-size-18"></i> Faturar </button>
                        
                        <button type="button" class="btn btn-success"  title="Imprimir" v-if="faturaModal.id" @click.prevent="imprimir(faturaModal.parcelas[0].id,'simplificado')"><i class="mdi mdi-printer font-size-18"></i> Simplificado </button>
                        <button type="button" class="btn btn-success"  title="Imprimir" v-if="faturaModal.id" @click.prevent="imprimir(faturaModal.parcelas[0].id,'detalhado')"><i class="mdi mdi-printer font-size-18"></i> Detalhado </button>
                    </div>                        
                </div>
            </div><!-- /.modal-content -->
        </div><!-- /.modal-dialog -->
    </div>


</div>

    
</template>

<script>
import { ContaReceberService, OrdemServicoService, ParceiroService, PlanoContasService } from '../../../services'
import { required, maxLength, minValue } from "vuelidate/lib/validators";
import moment from 'moment';

const planoContaService = new PlanoContasService();
const parceiroService = new ParceiroService();
const contasReceberService = new ContaReceberService();
const servicoService = new OrdemServicoService();


export default {
    props:{
        rota : {
            type : String,
            default: ''
        }
    },
    data(){
        return{
            dados: [],
            caixas: [],            
            parceiros:[],
            
            filtro:{
                ordenar: 'devolucao_data',
                ordem: 'asc',
                page: 1,
                per_page: 100,
                texto: '',
                pendente_pagamento: 1,
                parceiro_id: null,
                devolucao_data_final: null, 
                devolucao_data_inicial: null,
            },
            paginacao: {
                current_page: 0,
                total: 0,
                last_page: 0,
                from: 0,
                to: 0,
                per_page: 100
            },
            debounce: null,
            itemModal: null,
            sucessoModal: null,
            erroModal: null,

            faturaModal: {
                id:null,
                parceiro: null,
                parceiro_id: null,
                valor_total: 0,
                ordens_servico: [],
                historico: '',
            },

            baixar_todos: false
        }
    },
    validations() {
        return {
            faturaModal: {
                ordens_servico: {required },
                historico: {maxLength: maxLength(100)},
                valor_total: {required, minValue: minValue(0.01)},
                parceiro_id: {required},
                
                parcelas: {
                    required,
                    $each: {
                        parcela: {required},
                        parcelas: {required},
                        valor: {required, minValue: minValue(0.0001)},
                        data_vencimento: {required},
                        
                    }
                }
            },
        }
    },
    methods:{
        debounceBusca: function(){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.navegar(1);
            }, 800);
        },
        
        buscar:function(){
            this.dados = [];
            let obj = {...this.filtro};
            if(obj.devolucao_data_inicial)
                obj.devolucao_data_inicial = moment(obj.devolucao_data_inicial).format('YYYY-MM-DD');
            if(obj.devolucao_data_final)
                obj.devolucao_data_final = moment(obj.devolucao_data_final).format('YYYY-MM-DD');

            servicoService.listar(obj).then(
                response => {
                    this.dados = response.data.data;
                    this.paginacao = response.data;
                    this.filtro.page = response.data.current_page;
                    this.paginacao.per_page = response.data.per_page;
                }
            )
        },
       
        buscarParceiroDebounce: function(text, loading){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.buscarParceiro(text, loading)
            }, 800);
            
        },

        setClienteFiltro (cliente) {
            if(cliente){
                this.parceiros = [{...cliente}]
                this.filtro.parceiro_id = this.parceiros[0].id
            }
        },

        buscarParceiro: function(text, loading){
            let filtro = {texto: text, per_page:40};
            parceiroService.listar(filtro).then( response => this.parceiros = response.data.data)
        },

        abrirModalFatura:function(){
            this.faturaModal.parcelas = [{
                valor: this.faturaModal.valor_total,
                data_vencimento: null,
                parcela: 1,
                parcelas: 1
            }]
            this.faturaModal.ordens_servico = this.dados.filter(x => x.baixar);
            this.faturaModal.parceiro = this.faturaModal.ordens_servico[0].parceiro;
            this.faturaModal.parceiro_id = this.faturaModal.parceiro.id;

            
        },
        
        salvarFatura: function(){
            this.sucessoModal = null;
            this.erroModal = null;
            this.faturaModal.parcelas.map((x,i) => {
                x.parcela = i+1,
                x.parcelas = this.faturaModal.parcelas.length
                return x;
            });
            
            this.$v.faturaModal.$touch();
            if(this.$v.faturaModal.$invalid){
                this.erroModal = this.$v.faturaModal
                return 0;
            }
            
            
            servicoService.salvarFaturamento({...this.faturaModal})
                .then( response => {
                    this.sucessoModal = `Fatura ${response.data.id} Criada com Sucesso!`
                    this.faturaModal.id = response.data.id;
                    this.faturaModal.parcelas = response.data.parcelas;
                    this.buscar();
                    this.$forceUpdate();
                }).catch( erro => this.erroModal = erro)            
        },

        
        navegar: function(pagina){
            this.filtro.page = pagina;
            this.buscar();
        },
        buscarCaixas: function(){
            let filtro = {tipo: "TRANSFERENCIA", texto: '', per_page:40};
            planoContaService.listar(filtro).then(
                response => this.caixas = response.data.data.map(x => {
                    x.nome = x.codigo + ' - ' + x.nome;
                    return x;
                })
            );
        },

        selecionarTodos: function(){
            if(this.baixar_todos){
                this.dados = this.dados.map(x => {x.baixar = true; return x});
            }else{
                this.dados = this.dados.map(x => {x.baixar = false; return x});
            }
            this.selecionouItem();
        },

        selecionouItem: function(){
            let todos_selecionados = this.dados.filter(x => x.baixar).length == this.dados.length;            
            this.baixar_todos = todos_selecionados;

            this.faturaModal.valor_total = this.dados.filter(x => x.baixar).reduce((a,c) => Number(a) + Number(c.valor_total), 0)
            
            this.faturaModal.ordens_servico = this.dados.filter(x => x.baixar);
        },
        

        money: function(){
            return  {
                decimal: ',',
                thousands: '.',
                prefix: this.$store.state.moeda + ' ',
                suffix: '',
                precision: 2,
                masked: false
            }
        },
        desabilitaDataFutura: function(data) {
            let hoje = new Date();
            return data > hoje
        },
        desabilitaDataFuturaVencimento: function(data) {
            if(!this.filtro.devolucao_data_final)
                return false;
            return this.filtro.devolucao_data_final <= data
        },
        desabilitaDataPassadaVencimento: function(data) {
            if(!this.filtro.devolucao_data_inicial)
                return false;
            return this.filtro.devolucao_data_inicial >= data
        },
       
        desabilitaDataPassada: function(data) {
            let hoje = new Date();
            return data < hoje
        },
        
        imprimir: function(parcelaId,tipo){
            let url = contasReceberService.imprimir(parcelaId)+'?tipo_impressao='+tipo;
            window.open(url,'_blank');
        },
        imprimir_listagem: function(tipo){
            let obj = {...this.filtro};
            if(obj.devolucao_data_inicial)
                obj.devolucao_data_inicial = moment(obj.devolucao_data_inicial).format('YYYY-MM-DD');
            if(obj.devolucao_data_final)
                obj.devolucao_data_final = moment(obj.devolucao_data_final).format('YYYY-MM-DD');
            obj.tipo_impressao = tipo
            
            let url = servicoService.imprimirPendentesCobranca(obj);
            window.open(url,'_blank');
        }
        
    },
    watch: {
        
        'filtro.ordenar': function (val) {
            this.buscar()
        }, 
        'filtro.ordem': function (val) {
            this.buscar()
        }, 
        'filtro.page': function (val) {
            this.buscar()
        }, 
        'filtro.per_page': function (val) {
            this.buscar()
        }, 
        'filtro.classificacao': function (val) {
            this.debounceBusca()
        }, 
        'filtro.texto': function (val) {
            this.debounceBusca()
        }, 
        'filtro.parceiro_id': function (val) {
            this.debounceBusca()
        }, 
        'filtro.devolucao_data_final': function (val) {
            this.debounceBusca()
        }, 
        'filtro.devolucao_data_inicial': function (val) {
            this.debounceBusca()
        }, 
     
    },
    mounted(){
        
      
        setTimeout(() => {
            this.buscar();
        }, 300);
        
        setTimeout(() => {
            this.buscarCaixas();
        }, 200);
        
        
    },
    computed: {
        
        clientes_diferentes(){
            return ([...new Set(this.dados.filter(x => x.baixar).map(x => x.parceiro_id))].length > 1)
        }
    }
}
</script>
<style scoped>
 
</style>